<template>
  <div class="makeInvoice">
    <div class="makeInvoice-header">{{ $route.meta.makeInvoiceData.makeInvoiceTitle }}</div>
    <div class="makeInvoice-body">
      <el-form
        ref="makeInvoiceForm"
        label-width="185px"
        autocomplete="on"
        label-position="right"
        :model="makeInvoiceForm"
        :rules="makeInvoiceFormRules"
      >
        <el-row>
          <el-col :span="12">
            <el-form-item label="买方单位/个人住址">
              <el-input
                v-model="makeInvoiceForm.buyerAddress"
                ref="buyerAddress"
                placeholder="买方单位/个人"
                name="buyerAddress"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="买方单位代码/身份证号码">
              <el-input
                v-model="makeInvoiceForm.buyerCode"
                ref="buyerCode"
                placeholder="单位代码/身份证号码"
                name="buyerCode"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="买方单位/个人名称">
              <el-input
                v-model="makeInvoiceForm.buyerName"
                ref="buyerName"
                placeholder="买方单位/个人住址"
                name="buyerName"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话">
              <el-input
                v-model="makeInvoiceForm.buyerPhoneNumb"
                ref="buyerPhoneNumb"
                placeholder="电话"
                name="buyerPhoneNumb"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="卖方单位/个人住址">
              <el-input
                v-model="makeInvoiceForm.sellerAddress"
                ref="sellerAddress"
                placeholder="卖方单位/个人"
                name="sellerAddress"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="卖方单位代码/身份证号码">
              <el-input
                v-model="makeInvoiceForm.sellerCode"
                ref="sellerCode"
                placeholder="卖方单位代码/身份证号码"
                name="sellerCode"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="卖方单位/个人名称">
              <el-input
                v-model="makeInvoiceForm.sellerName"
                ref="sellerName"
                placeholder="卖方单位/个人住址"
                name="sellerName"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话">
              <el-input
                v-model="makeInvoiceForm.sellerPhoneNumb"
                ref="sellerPhoneNumb"
                placeholder
                name="sellerPhoneNumb"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label="车牌号码">
              <el-input
                v-model="makeInvoiceForm.carNumb"
                ref="carNumb"
                placeholder="车牌号码"
                name="carNumb"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="登记证号">
              <el-input
                v-model="makeInvoiceForm.registerNumber"
                ref="registerNumber"
                placeholder="登记证号"
                name="registerNumber"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="车辆类型">
              <el-input
                v-model="makeInvoiceForm.carType"
                ref="carType"
                placeholder="车辆类型"
                name="carType"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label="车架号/识别码">
              <el-input
                v-model="makeInvoiceForm.carFrameCode"
                ref="carFrameCode"
                placeholder="车架号/车辆识别代码"
                name="carFrameCode"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="厂牌类型">
              <el-input
                v-model="makeInvoiceForm.carLabelType"
                ref="carLabelType"
                placeholder="厂牌型号"
                name="carLabelType"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="转入地车辆管理所名称">
              <el-input
                v-model="makeInvoiceForm.vehicleManagement"
                ref="vehicleManagement"
                placeholder="转入地车辆管理所名称"
                name="vehicleManagement"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="车辆价格(大写)">
              <el-input
                v-model="makeInvoiceForm.carPrice"
                ref="carPrice"
                placeholder="车辆价格(大写)"
                name="carPrice"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="小写">
              <el-input
                v-model="makeInvoiceForm.totalPriceLower"
                ref="totalPriceLower"
                placeholder="小写"
                name="totalPriceLower"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="单位名称">
              <el-input
                v-model="makeInvoiceForm.organizationName"
                ref="organizationName"
                placeholder="单位名称"
                name="organizationName"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="单位类型">
              <el-select
                ref="trNode"
                disabled
                v-model="makeInvoiceForm.organizationType"
                @change="((val) => { organizationAction(val) })"
                placeholder="单位类型"
              >
                <el-option label="二手交易市场" :value="0"></el-option>
                <el-option label="拍卖单位" :value="1"></el-option>
                <el-option label="经营单位" :value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="单位地址">
              <el-input
                v-model="makeInvoiceForm.organizationAddress"
                ref="organizationAddress"
                placeholder
                name="organizationAddress"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="纳税人识别号">
              <el-input
                v-model="makeInvoiceForm.payerIdentify"
                ref="payerIdentify"
                placeholder="纳税人识别号"
                name="payerIdentify"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="开户银行、帐号">
              <el-input
                v-model="makeInvoiceForm.bankAccount"
                ref="bankAccount"
                placeholder="开户银行、帐号"
                name="bankAccount"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话">
              <el-input
                v-model="makeInvoiceForm.organizationTelephone"
                ref="organizationTelephone"
                placeholder="电话"
                name="organizationTelephone"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="备注">
              <el-input
                v-model="makeInvoiceForm.remark"
                ref="remark"
                placeholder="备注"
                name="remark"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="开票人">
              <el-input
                v-show="!isSignatureImg"
                v-model="makeInvoiceForm.drawer"
                ref="drawer"
                placeholder="开票人"
                name="drawer"
                tabindex="1"
                disabled
                autocomplete="on"
              ></el-input>
              <img
                @click="isSignatureShow = true"
                :src="signatureImg"
                class="signatureImg"
                v-show="isSignatureImg"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <div class="makeInvoice-submit">
          <el-row>
            <!--<el-col :span="8">
              <el-form-item>
                <el-button type="primary" @click="onSubmit">开票</el-button>
              </el-form-item>
            </el-col>-->
            <el-col :span="3">
              <el-form-item>
                <el-button type="primary" @click="doCXKCFPwebservice">库存查询</el-button>
              </el-form-item>
            </el-col>
            <el-col :span="3">
              <el-form-item>
                <el-button type="primary" @click="doCXHSSBZTwebservice">金税盘状态查询</el-button>
              </el-form-item>
            </el-col>
            <el-col :span="3">
              <el-form-item>
                <el-button type="primary" @click="doDYFPwebservice">补打发票</el-button>
              </el-form-item>
            </el-col>
            <el-col :span="3">
              <el-form-item>
                <el-button type="primary" @click="doZFFPwebservice">作废发票</el-button>
              </el-form-item>
            </el-col>
            <el-col :span="3">
              <el-form-item>
                <el-button type="primary" @click="doCXFPwebservice">查询发票</el-button>
              </el-form-item>
            </el-col>
            <el-col :span="3">
              <el-form-item>
                <el-button type="primary" @click="onErweimaPrint">扫码打印</el-button>
                
              </el-form-item>
            </el-col>
            
            <el-col :span="3">
              <el-form-item>
                <el-button>取消</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </el-form>

      <el-dialog
        ref="allocDialog"
        :title="erweimaTitle"
        :visible="erweimaVisible"
        width="720px"
        :before-close="erweimaClose"
        custom-class="radius-active"
        :close-on-click-modal="false"
      >
        <input
          id="erweimaInput"
          v-model="erweima"
          ref="erweima"
          name="erweima"
          autocomplete="on"
          class="erweimaInput"
        />
        <div slot="footer" class="dialog-footer">
          <el-button @click="erweimaAction">打印发票</el-button>
          <el-button type="primary" @click="erweimaDone">返回</el-button>
        </div>
      </el-dialog>
      <el-dialog></el-dialog>
    </div>
    <div class="isEqualCanvas" v-show="false">
      <canvas id="isEqualCanvas"></canvas>
    </div>
    <div class="signatureBox" v-show="isSignatureShow">
      <div class="canvasBox" ref="canvasHW">
        <canvas
          @touchstart="touchStart"
          @touchmove="touchMove"
          @touchend="touchEnd"
          @mousedown="mouseDown"
          @mousemove="mouseMove"
          @mouseup="mouseUp"
          ref="canvasF"
        ></canvas>

        <div class="btnBox">
          <el-button type="default" @click="closeCanvas">取消</el-button>
          <el-button type="default" @click="overwrite">清除</el-button>
          <el-button type="primary" @click="commit">提交</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getInvoiceInfo, doKPXTWebService } from "@/api/user";
import { numTranslateToUpperCN, setStorage, getStorage } from "@/utils/auth";
import { Message } from "element-ui";
export default {
  name: "MakeInvoice",
  data() {
    return {
      erweimaTitle: "",
      erweimaVisible: false,
      erweima: "",
      isSignatureShow: false,
      isSignatureImg: false,
      signatureImg: "",
      points: [],
      startX: 0,
      startY: 0,
      moveY: 0,
      moveX: 0,
      endY: 0,
      endX: 0,
      w: null,
      h: null,
      imgData: "",
      isDown: false,
      canvasBoard: null,
      canvasContext: null,
      makeInvoiceForm: {
        buyerAddress: "",
        buyerCode: "",
        buyerName: "",
        buyerPhoneNumb: "",
        sellerAddress: "",
        sellerCode: "",
        sellerName: "",
        sellerPhoneNumb: "",
        carNumb: "",
        registerNumber: "",
        carType: "",
        carFrameCode: "",
        carLabelType: "",
        vehicleManagement: "",
        carPrice: "",
        totalPriceLower: "",
        organizationName: "",
        organizationType: "",
        organizationAddress: "",
        payerIdentify: "",
        bankAccount: "",
        organizationTelephone: "",
        remark: "",
        drawer: ""
      },
      makeInvoiceFormRules: {}
    };
  },
  mounted() {
    this.canvasBoard = this.$refs.canvasF;
    this.canvasBoard.height = 480;
    this.canvasBoard.width = 760;
    this.canvasContext = this.canvasBoard.getContext("2d");
    const inputEle = this.$refs.drawer.$el.querySelector("input");

    inputEle.addEventListener(
      "focus",
      () => {
        this.isSignatureShow = true;
      },
      false
    );
    /*
    if (Object.keys(this.$route.params).length === 0) {
      this.$router.push({ path: "/schedule" });
    } else {
      sessionStorage.setItem("invoiceData", JSON.stringify(this.$route.params));
    }*/
  },
  methods: {
    // Computer event -- Mouse down
    mouseDown(ev) {
      ev = ev || event;
      ev.preventDefault();

      let obj = {
        x: ev.offsetX,
        y: ev.offsetY
      };

      this.startX = obj.x;
      this.startY = obj.y;
      this.canvasContext.beginPath();
      this.canvasContext.moveTo(this.startX, this.startY);
      this.canvasContext.lineTo(obj.x, obj.y);
      this.canvasContext.stroke();
      this.canvasContext.closePath();
      this.points.push(obj);
      this.isDown = true;
    },

    // Mobile event -- Touch start
    touchStart(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (ev.touches.length === 1) {
        let obj = {
          x: ev.targetTouches[0].clientX,
          y: ev.targetTouches[0].clientY - 48
        };

        this.startX = obj.x;
        this.startY = obj.y;
        this.canvasContext.beginPath();
        this.canvasContext.moveTo(this.startX, this.startY);
        this.canvasContext.lineTo(obj.x, obj.y);
        this.canvasContext.stroke();
        this.canvasContext.closePath();
        this.points.push(obj);
      }
    },

    // Mobile -- Mouse move
    mouseMove(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (this.isDown) {
        let obj = {
          x: ev.offsetX,
          y: ev.offsetY
        };

        this.moveY = obj.y;
        this.moveX = obj.x;
        this.canvasContext.beginPath();
        this.canvasContext.moveTo(this.startX, this.startY);
        this.canvasContext.lineTo(obj.x, obj.y);
        this.canvasContext.stroke();
        this.canvasContext.closePath();
        this.startY = obj.y;
        this.startX = obj.x;
        this.points.push(obj);
      }
    },

    // Mobile event -- Touch move
    touchMove(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (ev.touches.length === 1) {
        let obj = {
          x: ev.targetTouches[0].clientX,
          y: ev.targetTouches[0].clientY - 48
        };

        this.moveY = obj.y;
        this.moveX = obj.x;
        this.canvasContext.beginPath();
        this.canvasContext.moveTo(this.startX, this.startY);
        this.canvasContext.lineTo(obj.x, obj.y);
        this.canvasContext.stroke();
        this.canvasContext.closePath();
        this.startY = obj.y;
        this.startX = obj.x;
        this.points.push(obj);
      }
    },

    // Computer event -- Mouse up
    mouseUp(ev) {
      ev = ev || event;
      ev.preventDefault();

      let obj = {
        x: ev.offsetX,
        y: ev.offsetY
      };

      this.canvasContext.beginPath();
      this.canvasContext.moveTo(this.startX, this.startY);
      this.canvasContext.lineTo(obj.x, obj.y);
      this.canvasContext.stroke();
      this.canvasContext.closePath();
      this.points.push(obj);
      this.points.push({ x: -1, y: -1 });
      this.isDown = false;
    },

    // Mobile event TouchEnd
    touchEnd(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (ev.touches.length === 1) {
        let obj = {
          x: ev.targetTouches[0].clientX,
          y: ev.targetTouches[0].clientY - 48
        };

        this.canvasContext.beginPath();
        this.canvasContext.moveTo(this.startX, this.startY);
        this.canvasContext.lineTo(obj.x, obj.y);
        this.canvasContext.stroke();
        this.canvasContext.closePath();
        this.points.push(obj);
        this.points.push({ x: -1, y: -1 });
      }
    },
    // Over write
    overwrite() {
      this.canvasContext.clearRect(
        0,
        0,
        this.$refs.canvasF.width,
        this.$refs.canvasF.height
      );
      this.points = [];
    },
    closeCanvas() {
      if (this.isSignatureImg === true) {
        this.isSignatureImg = true;
        this.isSignatureShow = false;
      } else {
        this.isSignatureImg = false;
        this.isSignatureShow = false;
      }
    },
    // Commit
    commit() {
      // this.$refs.mySignature.src = this.$refs.canvasF.toDataURL('image/png')
      const image = this.$refs.canvasF.toDataURL("image/png");
      const isEqual = this.isCanvasEquals(this.$refs.canvasF);
      if (isEqual) {
        Message({
          message: "签名不能为空，请重新签名!",
          type: "error",
          duration: 3 * 1000
        });
        this.overwrite();
      } else {
        this.overwrite();
        this.signatureImg = image;
        this.isSignatureImg = true;
        this.isSignatureShow = false;
      }
    },
    isCanvasEquals(cavs) {
      const blank = document.createElement("canvas");
      blank.width = cavs.width;
      blank.height = cavs.height;
      return blank.toDataURL() == cavs.toDataURL();
    },
    onErweimaPrint() {
      this.erweimaTitle = "扫码打印";
      this.erweimaVisible = true;
      this.$nextTick(() => {
        this.$refs.erweima.focus();
      });
    },
    erweimaClose() {
      this.erweimaVisible = false;
    },
    erweimaDone() {},
    erweimaAction() {
      const invoiceId = this.$refs.erweima.value;
      this.$refs.erweima.value = "";
      this.erweimaTitle = "";
      this.erweimaVisible = false;
      this.doInvoiceInfo(invoiceId);
    },
    /** 打印补打发票操作 */
    doDYFPwebservice() {
      const { FPDM, FPHM } = getStorage("invoice") ? getStorage("invoice") : ({"FPDM": "140110710000", "FPHM": "20000002"})
      doKPXTWebService(
        {
          CONTROL: {
            XFSH: "15000119780514458045", // 销方税号 字符20
            XFJH: "0", // 销方机号 数字3
            FQFIP: "192.168.20.73" // 操作提交方，发起方 IP
          },
          METHOD: "DYFP", // 打印发票
          METHODDATA: {
            DATAINFO: {
              // 数据内容
              FPZL: "42", // 发票种类 数值 长度 10 专用发票 2 –普通发票 42-二手车
              FPDM: FPDM, // 发票代码 字符 10
              FPHM: FPHM, // 发票号码 数字 8
              DYLB: "0", // 打印类别 0 打印发票 1 打印清单
              SFXSQR: "0" // 是否显示确认对话框 0 不显示 1 显示
            }
          }
        },
        "DYFP"
      ).then(resp => {
        console.log(resp);
      });
    },
    /** 查询发票操作 */
    doCXFPwebservice() {
      const { FPDM, FPHM } = getStorage("invoice") ? getStorage("invoice") : ({"FPDM": "140110710000", "FPHM": "20000002"})
      debugger
      doKPXTWebService(
        {
          CONTROL: {
            XFSH: "15000119780514458045", // 销方税号 字符20
            XFJH: "0", // 销方机号 数字3
            FQFIP: "192.168.20.73" // 操作提交方发起方 IP
          },
          METHOD: "CXFP", // 查询发票
          METHODDATA: {
            DATAINFO: {
              // 数据内容
              ZYXTDJH: "", // 自有系统单据号 本项与发票种类、发票代码、发 票号码这三项不能同时为空
              FPZL: "42", // 发票种类 数值 长度 1 0 专用发票 2 –普通发票 42-二手车
              FPDM: FPDM, // 发票代码 字符 10
              FPHM: FPHM // 发票号码 数字 8
            }
          }
        },
        "CXFP"
      ).then(resp => {
        console.log(resp);
      });
    },

    /** 查询库存发票操作 */
    doCXKCFPwebservice() {
      doKPXTWebService(
        {
          CONTROL: {
            XFSH: "15000119780514458045", // 销方税号
            XFJH: "0", // 销方机号
            FQFIP: "192.168.20.73" // 发起方IP
          },
          METHOD: "CXKCFP", // 查询库存发票
          METHODDATA: {
            DATAINFO: {
              FPZL: "42" // 发票种类 42-二手车
            }
          }
        },
        "CXKCFP"
      ).then(resp => {
        console.log(resp);
      });
    },

    /** 作废发票操作 */
    doZFFPwebservice() {
      const { FPDM, FPHM } = getStorage("invoice") ? getStorage("invoice") : ({"FPDM": "140110710000", "FPHM": "20000002"})
      doKPXTWebService(
        {
          CONTROL: {
            XFSH: "15000119780514458045", // 销方税号
            XFJH: "0", // 销方机号
            FQFIP: "192.168.20.73" // 发起方IP
          },
          METHOD: "ZFFP", // 作废发票
          METHODDATA: {
            DATAINFO: {
              FPZL: "42", // 发票种类
              FPDM: FPDM, // 发票代码
              FPHM: FPHM // 发票号码
            }
          }
        },
        "ZFFP"
      ).then(resp => {
        console.log(resp);
      });
    },
    /** 查询金税设备状态 */
    doCXHSSBZTwebservice() {
      doKPXTWebService(
        {
          CONTROL: {
            XFSH: "15000119780514458045", // 销方税号
            XFJH: "0", // 销方机号
            FQFIP: "192.168.20.73" // 发起方IP
          },
          METHOD: "CXJSSBZT", // 查询金税设备状态
          METHODDATA: {}
        },
        "CXJSSBZT"
      ).then(resp => {
        console.log(resp);
      });
    },
    doInvoiceInfo(id) {
      // const { objId } = JSON.parse(sessionStorage.getItem('invoiceData'))
      getInvoiceInfo(id).then(resp => {
        console.log(resp.data);
        for (const key in resp.data) {
          if (key === "carPrice") {
            this.makeInvoiceForm[key] = numTranslateToUpperCN(resp.data[key]);
          } else {
            this.makeInvoiceForm[key] = resp.data[key];
          }
        }
        debugger
        this.onSubmit(resp.data);
      });
    },
    onSubmit(data) {
      /** 开具发票 */
      const {
        buyerName, 
        buyerCode, 
        buyerAddress, 
        buyerPhoneNumb,
        sellerName,
        sellerAddress,
        sellerCode,
        sellerPhoneNumb,
        carNumb,
        registrationNumb,
        carLabelType,
        carFrameCode,
        carType,
        carPrice
        } = data;
        
      doKPXTWebService(
        {
          "CONTROL": {
            "XFSH": "15000119780514458045",
            "XFJH": "0",
            "FQFIP": "192.168.20.73"
          },
          "METHOD": "KJESCFP",
            "METHODDATA": {
            "DATAINFO": {
            "FPZL": "42", // 发票种类，42 –二手车发票
            "GFMC": buyerName, // 买方名称，字符72
            "GFDM": buyerCode, // 买方单位代码/身份证号码，字符22
            "GFDZ": buyerAddress, // 买方地址，字符80
            "GFDH": buyerPhoneNumb, // 买方电话，字符20
            "XFMC": sellerName, // 卖方名称，字符72
            "XFDM": sellerCode, // 卖方单位代码/身份证号码，字符22
            "XFDZ": sellerAddress, // 卖方地址，字符80
            "XFDH": sellerPhoneNumb, // 卖方电话，字符20
            "CPZH": carNumb, // 车牌照号，字符20
            "DJZH": registrationNumb, // 登记证号，字符20
            "CLLX": carType, // 车辆类型，字符40
            "CJH": carFrameCode, // 车架号/车辆识别代码，字符23
            "CPXH": carLabelType, // 厂牌型号，字符60
            "GOODSNOVER": "33.0", // 商品税收分类编码版本号 非空
            "GOODSTAXNO": "109030602", // 商品税收分类编码—非空
            "TAXPRE": "0", // 是否享受优惠政策，0：不享受，1：享受 二手车不交税，为0
            "TAXPRECON": "", // 优惠政策 二手车不交税，为空
            "ZERNOTAX": "", // 零税率标识 二手车不交税，为空
            "CROPGOODSNO": "", // 企业自编码
            "CGSMC": "江岸车管所", // 车管所名称
            "HJJE": carPrice.toString(), // 车价合计
            "JYPMMC": "湖北航信", // 经营拍卖单位
            "JYPMSH": "12345678901234567890", // 经营拍卖单位纳税人识别号
            "JYPMYHZH": "6226123456789012346", // 经营拍卖单位开户银行，账号
            "JYPMDZ": "武汉市洪山区中北路81号", // 经营拍卖单位地址
            "JYPMDH": "027-5236215", // 经营拍卖单位电话
            "SCMC": "湖北航信", // 二手车市场
            "SCSH": "6226123456789012346", // 二手车市场纳税人识别号
            "SCDZ": "", // 二手车市场 地址
            "SCYHZH": "", // 二手车市场开户行,账号
            "SCDH": "", // 二手车市场电话
            "BZ": "二手车", // 备注
            "KPR": "杨明", // 开票人
            "SFXSQR": "0"
            }
            }
        },
        "KJESCFP"
      ).then(resp => {
        const { FPDM, FPHM } = resp.DATAINFO;
        setStorage('invoice', { FPDM: FPDM, FPHM: FPHM })
      });
    },
    organizationAction(val) {
      console.log(val);
    }
  }
};
</script>
